.cell__wrapper {
  width: 100%;
  background-color: rgb(235, 165, 235);
  display: flex;
  align-items: center;
  font-size: 14px;
  border-bottom: 1px solid rgb(201, 92, 223);

  .name {
    margin-left: 20px;
    width: 150px;
  }
  .code {
    margin-left: 50px;
    width: 40px;
  }
  .mid {
    margin-left: 50px;
    width: 50px;
  }

  .delete {
    margin-left: 70px;
    height: 24px;
    padding: 4px;
    background-color: rgb(201, 92, 223);
    border-radius: 10px;
    cursor: pointer;
  }

  .delete:hover {
    background-color: rgb(102, 9, 121);
    color: white;

  }
}
